<template>
  <v-app id="mainEntry" v-scroll="isHomeInViewport">

    <v-main>
      
      <v-col align="middle" class="d-block pa-0">
        <v-col align="middle" class="screen" id="home">
          <div class="d-none d-xl-block">
            <v-app-bar
              :color="backColor"
              dark
              elevation=0
              :height="appBarHeight"
              fixed
            >
              <div class="d-flex align-center mx-auto">
                <div id="nomuTitle">NOMU</div>

                <appBarBar />
              </div>
              
            </v-app-bar>
            <v-img :src="require('./assets/nomu.png')" contain height="410" class="mb-14" :class="{ 'big_margin': isHomeInViewport()}" />
            <p class="mb-3" id="nomuIntro">WELCOME TO NOMU</p>
            <v-btn floating icon @click="clickButton"><v-img :src="require('./assets/headerdescer.png')" height="62" contain /></v-btn>
          </div>

          <div class="d-block d-xl-none">
            <v-app-bar
              :color="backColor"
              dark
              elevation=0
              :height="appBarHeight"
              fixed
            >
              <div class="d-flex align-center mx-auto">
                <div id="nomuTitle-md">NOMU</div>

                <appBarBar />
              </div>
              
            </v-app-bar>
            <v-img :src="require('./assets/nomu.png')" contain height="240" class="mb-7" :class="{ 'big_margin': isHomeInViewport()}" />
            <p class="mb-10" id="nomuIntro-md">WELCOME TO NOMU</p>
            <v-btn floating icon @click="clickButton"><v-img :src="require('./assets/headerdescer.png')" height="41" contain /></v-btn>
          </div>
        </v-col>

        <about-us purple-bg />
        <functionalities />
        <parameters purple-bg />
        <portfolio />
        <!-- // TODO botar portfolio e purple-bg no contact qd portfolio tiver pronto -->
        <contact />
      </v-col>
    </v-main>
  </v-app>
</template>

<script>
import AppBarBar from './components/AppBarBar.vue';
import AboutUs from './components/AboutUs.vue';
import Functionalities from './components/Functionalities.vue';
import Parameters from './components/Parameters.vue';
import Contact from './components/Contact.vue';
// import Portfolio from './components/Portfolio.vue';
// TODO botão que leva pro topo da página
// TODO fallbacks para tamanhos mínimos absolutos de fontes e etc
// TODO responsividade para mobile
export default {
  name: 'App',

  components: {
    AppBarBar,
    AboutUs,
    Functionalities,
    Parameters,
    Contact,
    // Portfolio
  },
  data:() => ({
    backColor: 'transparent',
    appBarHeight: 100,
  }),
  methods: {
    clickButton(){
      this.$vuetify.goTo('#aboutus',{easing:'easeInOutQuad', duration: 500})
    },
    isHomeInViewport() {
      if(!document.querySelector("#home")) return true
      const rect = document.querySelector("#home").getBoundingClientRect();
      console.log('in app:',rect.bottom > 60)
      if(rect.bottom > 50){
        this.backColor ='transparent'
        this.appBarHeight = 100
        return true
      }else{
        this.backColor ='#390360'
        this.appBarHeight = 60
        return false
      }
    },
  },
};
</script>

